import { Component, Prop, State, Element, h } from '@stencil/core';
import { CommentType, UserComments, Pagination } from './interfaces';
import anime from 'animejs';
import Helper from '../../Helpers/Helper';
@Component({
  tag: 'salla-comments',
  styleUrl: 'salla-comments.scss',
})
export class SallaComments {

  constructor() {
    salla.lang.onLoaded(() => {
      this.comment_title = salla.lang.get('blocks.comments.title')
      this.comment_name = salla.lang.get('blocks.comments.comment')
      this.noComments = salla.lang.get('pages.rating.no_ratings')
    })
  }

  /**
   * Page or product ID
   */
  @Prop() itemId!: number;

  /**
     * Load more text
     */
  @Prop() loadMoreText: string;

  /**
     * Load more text
     */
  @Prop() hideForm: boolean;

  /**
   * Comment Type
   */
  @Prop() type: CommentType.PAGE | CommentType.PRODUCT = CommentType.PAGE;

  @State() comments: UserComments[];
  @State() pagination: Pagination;
  @State() total: number;
  @State() showPlaceholder: boolean;
  @State() nextPage: string;

  private status: any;
  private btnLoader: any;
  private infiniteScroll: any;
  private wrapper: any;

  @Element() host: HTMLElement;

  // Translations
  @State() noComments: string = salla.lang.get('blocks.comments.no_comments');
  @State() comment_title = salla.lang.get('blocks.comments.title')
  @State() comment_name = salla.lang.get('blocks.comments.comment')
  @State() placeholder_text: string;

  // TOOD: it's a good idea to move this into lang.js
  // Pluralize a string based on the count
  private pluralize(phrases: string, count: number): string {
    const options = phrases.split('|');

    const conditions = [
      { condition: count === 0, index: 0 },
      { condition: count === 1, index: 1 },
      { condition: count === 2, index: 2 },
      { condition: count > 2 && count <= 10, index: 3 },
      { condition: count >= 11, index: 4 }
    ];

    const { index } = conditions.find(({ condition }) => condition) || { index: options.length - 1 };

    const selectedOption = options[index];
    return selectedOption.replace(':count', count.toString())
      .replace(/\{[0-9]+\}/g, '')
      .replace(/\[\d+,\d+\]|\[11,\*\]/g, '');
  }



  // Initiate infinite scroll
  private initiateInfiniteScroll() {
    if (!this.wrapper) {
      console.error('Wrapper is undefined. Cannot initiate infinite scroll.');
      return;
    }

    this.infiniteScroll = salla.infiniteScroll.initiate(this.wrapper, this.wrapper, {
      path: () => this.nextPage,
      history: true,
      nextPage: this.nextPage,
      scrollThreshold: false,
    }, true);

    this.infiniteScroll?.on('request', _response => {
      this.loading();
    });
    this.infiniteScroll?.on('load', response => {
      this.pagination = response.pagination;
      this.nextPage = typeof response.pagination.links == 'object' && !!response.pagination.links.next ? response.pagination.links.next : null;
      this.handleResponse(response).forEach(card => this.wrapper.append(card));

      let items = this.host.querySelectorAll('salla-comment-item:not(.animated):not(.s-comments-item-admin)');
      this.animateItems(items);
      this.loading(false);
    });
    this.infiniteScroll?.on('error', (e) => {
      salla.console.error('Error loading more comments:', e);
    });
  }

  // Show/hide loading
  private loading(isLoading = true) {
    let btnText = this.status?.querySelector('.s-button-text');
    if (btnText) {
      Helper.toggleElementClassIf(btnText, 's-button-hide', 's-button-show', () => isLoading);
      this.btnLoader.style.display = isLoading ? 'inherit' : 'none';
    }
  }

  // Animate newly added items
  private animateItems(items) {
    anime({
      targets: items,
      opacity: [0, 1],
      duration: 1200,
      translateY: [20, 0],
      delay: function (_el, i) {
        return i * 100;
      },
      easing: 'easeOutExpo',
      complete: function (_anim) {
        items.forEach(item => {
          item.classList.add('animated');
        })
      }

    })
  }

  // Get comment item HTML
  private getCommentHTML(comment) {
    const commentItem = document.createElement('salla-comment-item') as HTMLSallaCommentItemElement;
    commentItem.comment = comment;

    return commentItem;
  }



  // Parse response and return an array of comment items to be appended to the wrapper
  private handleResponse(response): Array<HTMLElement> {
    return response.data?.map(comment => this.getCommentHTML(comment)) || [];
  }



  // Load initial data
  private async loadInitialData() {
    this.loading();
    try {
      const resp = await (this.type == CommentType.PAGE
        ? salla.api.comment.getPageComments(this.itemId)
        : salla.api.comment.getProductComments(this.itemId));
      if (!resp.data || !resp.data.length) {
        this.showPlaceholder = false;
        this.loading(false);
        return;
      }
      this.comments = resp.data;
      this.pagination = resp.pagination;
      this.total = resp.pagination.total;
      this.nextPage = typeof resp.pagination.links == 'object' && !!resp.pagination.links.next ? resp.pagination.links.next : null;


      setTimeout(() => {
        this.handleResponse(resp).forEach(card => this.wrapper.append(card));
        this.initiateInfiniteScroll(); // Initiate infinite scroll after the initial data is loaded
        let items = this.wrapper.querySelectorAll('salla-comment-item:not(.animated)');
        this.animateItems(items);
      }, 100);
    } catch (error) {
      console.error('Error loading initial data:', error);
      this.showPlaceholder = true;
      this.loading(false);
    }
  }

  // Get next page
  async loadMore() {
    this.infiniteScroll?.loadNextPage();
  }

  render() {
    // We should show a different placeholder for pages and products (WIP)
    if (this.showPlaceholder) {
      return (
        <div>
          <h2 class="s-comments-title">{this.comment_title}</h2>
          {!this.hideForm && <salla-comment-form type={this.type} item-id={this.itemId}></salla-comment-form>}
          <div class="no-content-placeholder--comments">
            <i class="sicon-chat-bubbles text-5xl block mb-5 text-gray-400"></i>
            <p class="text-sm">{this.noComments}</p>
          </div>
        </div>
      )
    }
    return (
      <div class={`s-comments s-comments-${this.type}`}>
        <div class={`${this.type == CommentType.PAGE ? "s-comments-page-container" : "s-comments-container"}`} >
          <h2 class="s-comments-title">{this.comment_title}</h2>
          {!this.hideForm && <salla-comment-form type={this.type} item-id={this.itemId}></salla-comment-form>}

          {!!this.total && <h2 class="s-comments-count-label" innerHTML={this.pluralize(this.comment_name, this.total)}></h2>}
          <div ref={wrapper => this.wrapper = wrapper}></div>


          {this.nextPage && (
            <div class="s-infinite-scroll-wrapper" ref={status => this.status = status}>
              <button onClick={() => this.loadMore()} class="s-infinite-scroll-btn s-button-btn s-button-primary">
                <span class="s-button-text s-infinite-scroll-btn-text">{this.loadMoreText ? this.loadMoreText : salla.lang.get('common.elements.load_more')}</span>
                <span class="s-button-loader s-button-loader-center s-infinite-scroll-btn-loader" ref={btnLoader => this.btnLoader = btnLoader} style={{ "display": "none" }}></span>
              </button>
            </div>
          )}
        </div>
      </div>

    );
  }



  async componentWillLoad() {
    await this.loadInitialData(); // Load initial data before rendering the component
  }

}
